li {
  list-style-type: none; }

address, dl, fieldset, figure, ol, p, pre, ul {
  margin: 0;
  padding: 0; }

a {
  transition: .3s ease-in-out; }

a:hover {
  opacity: 0.9;
  text-decoration: none;
  letter-spacing: 2px; }

header {
  padding: 20px;
  background: #fff;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 9999;
  transition: .7s ease-in-out; }

.header .header__logo {
  font-family: 'Lobster', cursive;
  font-size: 1.8rem;
  color: #6E611D;
  transition: .7s; }

.header .header__logo a {
  color: #6E611D; }

header .header__nav ul {
  display: flex;
  font-weight: normal; }

header .header__nav ul a {
  color: #000; }

header .header__nav ul li {
  padding: 0 20px; }

.header__scrolled {
  background: #fff;
  box-shadow: #555 0px 8px 48px -8px; }

.header__scrolled .header__logo {
  font-size: 1.3rem; }

.section {
  padding: 80px 0;
  display: flex;
  justify-content: center; }

.section h2 {
  text-align: center; }

.section .section__wrapper {
  max-width: 1100px;
  padding: 0 20px; }

.hero {
  display: flex;
  justify-content: space-between; }

.hero .hero__texts {
  position: relative; }

.hero .hero__sub {
  color: #7A6E30;
  font-size: 1.4rem; }

.hero .hero__img {
  background: url(../images/hero.jpg) top right;
  background-position: top left;
  width: calc(76vw - (100vw - 100%));
  height: calc(100vh - 63px);
  min-height: 300px; }

.hero .hero__catch {
  display: inline-block;
  padding: 0;
  font-size: 6rem;
  font-weight: bold;
  line-height: 1.1;
  color: #7A6E30; }

.hero .origin {
  width: 19vw;
  overflow: hidden;
  position: absolute;
  top: 50%;
  transform: translate(0%, -50%);
  z-index: 999;
  left: 5vw; }

.hero .origin .hero__desc {
  color: #000; }

.hero .hero__desc {
  color: #fff;
  width: 550px;
  padding: 10px;
  line-height: 1.8;
  font-size: 0.8rem;
  /*background: rgba(0,0,0,0.4);*/ }

.hero .duplicated {
  position: absolute;
  color: #fff;
  top: 50%;
  transform: translate(0%, -50%);
  left: 5vw; }

.hero .duplicated .hero__sub {
  color: #fff; }

.hero .duplicated .hero__catch {
  color: #fff; }

.news .news__head {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  align-items: center; }

.news li {
  width: 600px;
  padding: 10px 0;
  border-bottom: 1px #ccc solid; }

.news .news__body {
  margin-top: 10px;
  font-size: 0.9rem; }

.news .news__date {
  font-size: 0.9rem;
  padding: 2px 4px;
  display: inline-table;
  background: #6E611D;
  color: #fff; }

.access .section__wrapper {
  width: 100%;
  max-width: initial;
  padding: 0;
  text-align: center; }

.access dl {
  display: inline-block;
  margin: 0 auto 20px auto;
  text-align: left; }

.access dd {
  margin-bottom: 10px; }

.access .uk-icon {
  margin-right: 10px; }

.contact .section__wrapper {
  width: 600px; }

.contact button {
  margin: 0 auto;
  display: block; }

footer {
  background: #41424E;
  color: #828287;
  padding: 30px 0;
  display: flex;
  justify-content: center; }

footer a {
  color: #fff; }

footer h3 {
  color: #fff; }

footer .footer__columns {
  margin: 0 30px; }

footer .footer__sns {
  display: flex; }

footer .footer__desc {
  font-size: 0.8rem;
  width: 500px; }

.concept {
  display: none; }

.only_pc {
  display: block; }

.only_sp {
  display: none; }

@media screen and (max-width: 640px) {
  .only_pc {
    display: none; }
  .only_sp {
    display: block; }
  body {
    font-size: 14px; }
  div,
  ul,
  li,
  input {
    max-width: 100%; }
  .section {
    padding: 40px 0; }
  .section .section__wrapper {
    max-width: 100%; }
  .hero {
    padding-top: 63px; }
  .hero .hero__texts {
    z-index: 2; }
  .hero .origin,
  .hero .hero__desc {
    max-width: initial; }
  .hero .hero__img {
    background-position: 1367px -240px; }
  .hero .hero__desc {
    width: 75vw; }
  .hero .hero__img {
    width: 76%; }
  .concept {
    display: block; }
  header nav {
    display: none; }
  header {
    justify-content: center;
    padding: 10px 20px; }
  .header__scrolled {
    display: flex;
    transition: .7s ease-in-out; }
  .hero {
    max-height: 100vh;
    margin: 0 auto;
    width: 100%;
    background-size: cover;
    background-position: 100px 0; }
  .hero .hero__catch {
    font-size: 3rem; }
  .hero .hero__wrapper {
    text-align: left; }
  .news .news__head {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column-reverse;
    align-items: center; }
  .news .news__head > p {
    width: 100%;
    text-align: left; }
  .news .news__date {
    padding: 0;
    background: none;
    font-weight: bold;
    color: #6E611D;
    font-size: 0.8rem; }
  footer {
    flex-wrap: wrap;
    padding: 30px; }
  footer .footer__columns {
    margin: 0; }
  footer .footer__columns:nth-child(1) {
    display: none; }
  footer .footer__columns:nth-child(1),
  footer .footer__columns:nth-child(2) {
    width: 100%; }
  footer .footer__columns:nth-child(3) {
    width: 100%;
    margin-top: 20px;
    text-align: center; }
  footer .footer__sns {
    justify-content: center; } }
